.footer{
  width: 100%;
  height: 13vw;
  background-color: #181B1D;
  display: flex;
  justify-content: space-around;
  align-items: center;

  .socials{
    width: 45%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    
  }

  a{
    width: 20%;
    height: 60%;
    
    img{
      width: 80%;
    }
  }
  .info{
    font-size: 1.55rem;
    height: 40%;
    text-align: center;
    color: white;
  }
}

@media only screen and (min-width: 1200px) {
  .footer {
    height: 10vw;
    .socials{
      width: 30%;
      a{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 5vw;
        
        img{
          max-height: 4.7vw;
          width: 5vw;
        }
      }
    }

    .info{
      display: flex;
      align-items: center;
      justify-content: center;
      
      width: 50%;
      font-size: 4vw ;
    }
  }
}